import codemirror, { Mode } from "codemirror";
import _ from "lodash";

import "codemirror/addon/mode/multiplex";
import "codemirror/addon/mode/simple";

declare type RuleExpTokenType = "input" | "rule" | "string" | "number"
declare type IRRuleExpTokenType = `ir-${RuleExpTokenType}`

declare type RuleParserState = {
    contextStack: RuleContextInfo[];
    lastChar?: string;
}

declare type RuleContextInfo = {
    tokenType: IRRuleExpTokenType;
    begin: RegExp;
    end: RegExp;
}

const regexp = {
    startContext: [
        { tokenType: "ir-string", begin: /"/, end: /"/ },
        { tokenType: "ir-input", begin: /\[/, end: /\]/ },
        { tokenType: "ir-rule", begin: /\{/, end: /\}/ },
        { tokenType: "ir-number", begin: /\d/, end: /[^\d.]/ },
    ] as readonly RuleContextInfo[],
    operator: /(\||\+|-|\*|\/|%|\^|=|<=?|>=?|AND|OR|<>|::|:)/,
};

const hintList = {
    function: [
        // String
        "CONCAT", "FIND", "FORMAT", "LEFT", "LOWER", "LPAD", "MID", "REPLACESTR", "REPT", "RIGHT", "RPAD", "SPLIT", "SQLCONCAT",
        "SQLSTR", "STRLEN", "TRIM", "TRIMLEFT", "TRIMRIGHT", "UPPER",
        // Date
        "ADDMONTHS", "AGE", "DATETERM", "DAYAFTER", "LASTDAYOFMONTH", "MONTHTERM", "SYSDATE", "SYSTIME", "WEEKDAY", "YEARDATEDIFF",
        // Math
        "ABS", "ACOS", "ASIN", "ATAN", "CEILING", "COS", "DEGREE", "FLOOR", "FMOD", "LN", "LOG", "RAD", "ROUND", "ROUNDDOWN", "ROUNDUP", "SIN", "SQRT", "TAN",
        // Util
        "ARRAY", "BOTTOM", "CAP", "CUP", "DISTINCT", "FILTER", "FINDONEOF", "GET", "IF", "LOOKUP", "MAX", "MFILTER", "MIN", "MTOP", "NORM",
        "PRODUCT", "PUT", "SORT", "STEPVAL", "SUM", "TONUMBER", "TOP",
        // Rule
        "CALLPARAM", "CODECONV", "ERROR", "EXEC", "EXECRULE", "GETRNAME", "SYSPROP",
    ],
    operator: [ "AND", "OR" ],
    property: [ "array", "NOROWS", "ISNULL" ],
    keyword: [ "T", "F" ],
};

codemirror.defineMode( "ir-rule-exp", ( config, options ) =>
{
    const tokenize = ( stream: codemirror.StringStream, state: RuleParserState ) =>
    {
        const pushContext = ( info: RuleContextInfo ) => state.contextStack.push( info );
        const peekContext = () => _.last( state.contextStack );
        const popContext = () =>
        {
            state.contextStack.pop();
            state.lastChar = undefined;
        };

        const curContext = peekContext( );

        // 현재 context 확인
        if ( !curContext )
        {
            // 현재 문자 얻기
            const ch = stream.next();

            // ch가 없는 경우 null 토큰 (문자열 끝)
            if ( !ch ) return null;

            state.lastChar = ch;

            // 현재 공백인 경우 eatSpace 리턴 null
            if ( /\s/.test( ch ) )
            {
                stream.eatSpace();
                return null;
            }

            const context = regexp.startContext
                .find( ( { begin } ) => begin.test( ch ) );

            if ( context )
            {
                pushContext( context );

                return context.tokenType;
            }
        }
        else
        {
            // 현재 문자 얻기
            const ch = stream.next();

            // ch가 없는 경우 null 토큰 (문자열 끝)
            if ( !ch ) return curContext.tokenType;

            if ( curContext.end.test( ch ) &&
                // 문자열의 escape인 경우 계속 진행
                !( curContext.tokenType === "ir-string" && state.lastChar === '\\' ) )
            {
                popContext();

                // number인 경우 backup 1
                if ( curContext.tokenType === "ir-number" )
                    stream.backUp( 1 );
            }

            state.lastChar = ch;

            return curContext.tokenType;
        }

        return null;
    };

    return {
        startState()
        {
            return {
                contextStack: [],
                lastChar: undefined,
            };
        },
        token( stream, state )
        {
            return tokenize( stream, state );
        },
    } as Mode<RuleParserState>;
} );

codemirror.defineSimpleMode( "db-rule-input-parser", {
    start: [ { regex: /.*/, token: "ir-input" } ],
} );

codemirror.defineSimpleMode( "db-rule-rule-parser", {
    start: [ { regex: /.*/, token: "ir-rule" } ],
} );

codemirror.defineSimpleMode( "db-rule-comment-parser", {
    start: [ { regex: /.*/, token: "ir-comment" } ],
} );

codemirror.defineMode( "ir-db-rule", ( config, _options ) =>
{

    return codemirror.multiplexingMode( codemirror.getMode( config, "text/x-mysql" ),
        {
            open: "<$$", close: "$$>",
            mode: codemirror.getMode( config, "text/ir-rule-exp" ),
            delimStyle: "ir-db-rule-delimiter",
        },
        {
            open: "<$@", close: "@$>",
            mode: codemirror.getMode( config, "text/db-rule-comment-parser" ),
            delimStyle: "ir-db-rule-comment",
        },
        {
            open: "<$", close: "$>",
            mode: codemirror.getMode( config, "text/ir-rule-exp" ),
            delimStyle: "ir-db-rule-delimiter",
        },
        {
            open: "[", close: "]",
            mode: codemirror.getMode( config, "text/db-rule-input-parser" ),
            delimStyle: "ir-db-rule-input-delimiter",
        },
        {
            open: "{", close: "}",
            mode: codemirror.getMode( config, "text/db-rule-rule-parser" ),
            delimStyle: "ir-db-rule-rule-delimiter",
        } );
} );

codemirror.defineMIME( "text/ir-rule-exp", "ir-rule-exp" );
codemirror.defineMIME( "text/ir-db-rule", "ir-db-rule" );
codemirror.defineMIME( "text/db-rule-input-parser", "db-rule-input-parser" );
codemirror.defineMIME( "text/db-rule-rule-parser", "db-rule-rule-parser" );
codemirror.defineMIME( "text/db-rule-comment-parser", "db-rule-comment-parser" );