import codemirror from "codemirror";
import _ from 'lodash';

import i18n from '@i18n';
import { IRComponent } from "@js-comp/base";

import "codemirror/mode/sql/sql";
import "codemirror/addon/hint/show-hint";
import "codemirror/addon/hint/sql-hint";
import "codemirror/addon/dialog/dialog";
import "codemirror/addon/scroll/annotatescrollbar";
import "codemirror/addon/search/search";
import "codemirror/addon/search/jump-to-line";
import "codemirror/addon/search/matchesonscrollbar";
import "codemirror/addon/search/searchcursor";
import "codemirror/mode/xml/xml";
import "./mode";

export declare type IREditorMode = keyof typeof editorModeMap;

const EmptyCallBack = ( () =>
{ /* nothing */ } );

const editorModeMap = {
    "ir-db-rule": "text/ir-db-rule",
    "ir-rule-exp": "text/ir-rule-exp",
    "sql-mysql": "text/x-mysql",
    "sql-oracle": "text/x-plsql",
    "xml": "text/html",
};

declare type Gutter = {
    className: string;
    style: string;
}

export interface IREditorArgs {
    contextElement: HTMLElement;
    mode?: IREditorMode;
    code?: string;
    keyEvents?: Record<string, ( ) => void>;
    gutters?: Array<Gutter | string>;
}

export class IREditor extends IRComponent
{
    editor: codemirror.Editor;

    private _showWhitespace = false;

    constructor( { contextElement, mode = "ir-db-rule", code = "", keyEvents, gutters }: IREditorArgs )
    {
        super( { contextElement } );
        this.contextElement = contextElement;

        contextElement.classList.add( "editor" );
        this.editor = codemirror( contextElement, {
            lineNumbers: true,
            gutters: gutters ?? [ "CodeMirror-linenumbers" ],
            mode: editorModeMap[mode] ?? "text/plain",
            theme: "idea",
            tabSize: 4,
            indentWithTabs: false,
            smartIndent: true,
            autofocus: true,
            extraKeys: {
                "Ctrl-Space": "autocomplete",
                "Ctrl-F": EmptyCallBack,
                "Shift-Ctrl-F": EmptyCallBack,
                "Tab": ( cm ) =>
                {
                    cm.replaceSelection( "    " );
                },
                ...keyEvents,
            },
            hintOptions: { },
            dragDrop: true,
            allowDropFileTypes: ["text/x-sql", "text/plain"],
            phrases: {
                "Search:": i18n.editor.search,
                "(Use /re/ syntax for regexp search)": i18n.editor.regex,
                "Replace:": i18n.editor.replace,
                "With:": i18n.editor.with,
                "Replace?": i18n.editor.replaceYN,
                "Yes": i18n.editor.yes,
                "No": i18n.editor.no,
                "All": i18n.editor.all,
                "Stop": i18n.editor.stop,
            },
        } );

        this.editor.on( "gutterClick", ( cm, n ) =>
        {
            this.onGutterClick( n );
        } );

        this.editor.setSize( "100%", "100%" );
        this.code = code;

        this.onDestroy = () => this.editor.getWrapperElement().remove();
    }

    get cursor()
    {
        const cursorFrom = this.editor.getCursor( "from" );
        const cursorTo = this.editor.getCursor( "to" );
        const cursorElement = ( this.contextElement.querySelector( ".CodeMirror-cursor" )
            ?? this.contextElement.querySelector( ".CodeMirror-selected" ) ) as HTMLElement | undefined;

        return {
            startY: cursorFrom.line,
            startX: cursorFrom.ch,
            endY: cursorTo.line,
            endX: cursorTo.ch,
            x: cursorElement?.offsetLeft ?? 0,
            y: cursorElement?.offsetTop ?? 0,
        };
    }

    get lineCount()
    {
        return this.editor.lineCount();
    }

    get selectedText()
    {
        return this.editor.getSelection();
    }

    get currentLine()
    {
        return this.editor.getDoc().getLine( this.cursor.startY );
    }

    get code()
    {
        return this.editor.getDoc().getValue();
    }

    get showWhitespace()
    {
        return this._showWhitespace;
    }

    set code( code: string )
    {
        this.editor.getDoc().setValue( code );
    }

    set showWhitespace( visible: boolean )
    {
        if ( visible === this._showWhitespace ) return;

        if ( visible )
        {
            // add overlay
            this.editor.addOverlay( {
                name: "ir-editor-show-whitespace",
                token: ( stream: codemirror.StringStream ) =>
                {
                    const peek = stream.peek();

                    stream.next();

                    return peek === " " ? `ir-editor-whitespace ir-editor-whitespace-${stream.pos % 2 === 0 ? "even" : "odd"}` : undefined;
                },
            } );
        }
        else
        {
            // remove overlay
            this.editor.removeOverlay( "ir-editor-show-whitespace" );
        }

        this._showWhitespace = visible;
    }

    addLineGutter( lineNumber: number, key: string, marker: HTMLElement )
    {
        this.editor.setGutterMarker( lineNumber, key, marker );
    }

    removeLineGutter( lineNumber: number, key: string )
    {
        this.editor.setGutterMarker( lineNumber, key, null );
    }

    addLineClass( lineNumber: number, key: "background" | "wrap", cls: string )
    {
        this.editor.addLineClass( lineNumber, key, cls );
    }

    removeLineClass( lineNumber: number, key: "background" | "wrap", cls: string )
    {
        this.editor.removeLineClass( lineNumber, key, cls );
    }

    getLineText( line: number )
    {
        return this.editor.getDoc().getLine( line );
    }

    openFindDialog( )
    {
        this.editor.execCommand( "findPersistent" );
    }

    getSearchCursor( pattern: string, startY = 0, startX = 0 )
    {
        return this.editor.getSearchCursor( pattern, { line: startY, ch: startX } );
    }

    openReplaceDialog( )
    {
        this.editor.execCommand( "replace" );
    }

    setSelection( startY: number, startX: number, endY: number, endX: number )
    {
        this.editor.focus();
        this.editor.setSelection( {
            line: startY,
            ch: startX,
        }, {
            line: endY,
            ch: endX,
        } );
    }

    getTextWithCursor( startY: number, startX: number, endY: number, endX: number )
    {
        const buffer: string[] = [];

        // endY 값 보정
        endY = Math.min( endY, this.lineCount - 1 );

        if ( startY === endY )
            buffer.push( this.getLineText( startY ).substring( startX, endX ) );
        else
        {
            buffer.push( this.getLineText( startY ).substring( startX ) );

            _.range( startY + 1, endY, 1 )
                .forEach( ( y ) => buffer.push( this.getLineText( y ) ) );

            buffer.push( this.getLineText( endY ).substring( 0, endX ) );
        }

        return buffer.join( "\n" );
    }

    getPreText( y: number, x: number )
    {
        return this.getTextWithCursor( 0, 0, y, x );
    }

    getPostText( y: number, x: number )
    {
        const lastText = this.getLineText( this.lineCount - 1 );
        return this.getTextWithCursor( y, x, this.lineCount - 1, lastText.length );
    }

    /* Event */
    onGutterClick( _lineNumber: number )
    {
        // nothing
    }
}